'use client'; import { useEffect, useState } from 'react'; import { getTenant } from '@/lib/tenants'; type Session={tenant:string|null,role:string}; export default function SessionInfo(){ const [s,setS]=useState<Session>({tenant:null,role:'user'}); useEffect(()=>{ fetch('/api/session',{cache:'no-store'}).then(r=>r.json()).then(setS).catch(()=>{}); },[]); const t=getTenant(s.tenant); return <div style={{opacity:.8,fontSize:12,marginBottom:8}}>Tenant: <strong>{t?.name??'—'}</strong> • Role: <strong style={{textTransform:'capitalize'}}>{s.role}</strong></div>; }
